table {
  border-color: lightgrey;
  border-width: 1px;
  border-radius: 5px;
}

th {
  background-color: #042630;
  border: 1px solid black;
  text-align: left;
  padding: 1em;
  color: whitesmoke;
}

h6 {
  color: #b58900;
}

td {
  color: lightgrey;
}

.bioInfo {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
}

.loader {
  position: relative;
  bottom: -3em;
  background: rgba(255, 255, 255, 0.2);
}
.loader img {
  position: relative;
  transform: translate(-50%, -50%);
}

.GitHubChart {
  display: flex;
  flex-grow: 2;
  justify-content: flex-end;
}

.GitHubChart img {
  /* justify-self: flex-end; */
  display: flex;
  justify-content: flex-end;
  object-fit: cover;
  display: block;
  width: 100%;
  margin-right: 0.9em;
  text-align: center;
  color: white;
}

.githubBasicStats {
  margin-right: auto;
  padding-left: 1em;
  padding-right: 0.5em;
}

.GitHubIcon {
  width: 8em;
  border-radius: 5px;
}

a:link {
  color: #268bd2;
}

a:visited {
  color: #6c71c4;
}

@media only screen and (max-width: 720px) {
  .GitHubChart {
    visibility: hidden;
  }
}
